.dashboard-b2b.homepageB2B {
  .homepageB2B__heading {
    .heading {
      margin-bottom: 0;
    }
    
    .heading__title--sub {
      margin-bottom: rem-calc(22);
    }

    .heading--small {
      .heading__title {
        font-size: rem-calc(28*2/3);
        @media #{$medium-up} {
          font-size: rem-calc(38);
        }
      }

      .heading__title--sub {
        font-size: rem-calc(28*2/3);
        @media #{$medium-up} {
          font-size: rem-calc(38);
        }
      }
    }

    @media #{$medium-up} {
      padding: rem-calc(100) 0;
    }
  }

  .btn-primary {
    font-size: rem-calc(16);
    .icon {
      font-size: rem-calc(25);
    }
  }
}
