.diary {
  &__list {
    margin: 0;
    list-style: none;

    li {
      @include display(flex);
      @include flex-wrap(nowrap);
      padding-bottom: rem-calc(20*2/3);
    }

    &__img {
      width: 32%;
      background-color: $white;

      img {
        display: block;
        width: 100%;
      }
    }
    &__content {
      width: 68%;
      padding: rem-calc(5*2/3) rem-calc(20*2/3);
      background-color: $white;

      p {
        margin-bottom: 0;
      }

      &--title {
        font-family: $font-bold;
        $font-size: 20;
        $line-height: 1.3;
        $lines-to-show: 2;
        a,
        p {
          font-size: rem-calc($font-size*2/3);
          line-height: $line-height;
        }
        p {
          color: $jet;
        }
      }

      &--description {
        $font-size: 20;
        $line-height: 1.4;
        $lines-to-show: 2;
        font-size: rem-calc($font-size*2/3);
        margin-top: rem-calc(5*2/3);
        line-height: $line-height;
      }

      // with text limit: very bad idea to limit because of very flexible width (landscape/portrait)
      /*
