    }
  }

  .btn-wrapper {
    padding-top: rem-calc(40);

    a {
      margin: 0;
    }
  }
}
@media #{$small-only} {
  .diary {
    .btn-wrapper {
      padding-top: rem-calc(20);

      a {
        width: 100%;
      }
    }
  }

}
@media #{$medium-up} {
  .diary {
    &__list {
      @include grid-row($behavior: nest);

      li {
        @include grid-column(
          $columns: 6
        );
      }

      &__content {
        padding: rem-calc(25) rem-calc(30);

        &--title {
          font-size: rem-calc(20);

          p {
            $font-size: 20;
            $line-height: 1.5;
            $lines-to-show: 2;
            font-size: rem-calc($font-size);
            line-height: $line-height;
          }

          /*p {
            $font-size: 20;
            $line-height: 1.5;
            $lines-to-show: 2;
            font-size: rem-calc($font-size);
            display: -webkit-box;
            max-height: rem-calc($font-size*$line-height*$lines-to-show);
            line-height: $line-height;
            -webkit-line-clamp: $lines-to-show;
            -webkit-box-orient: vertical;
            overflow: hidden;
            text-overflow: ellipsis;
          }*/
        }

        p {
          margin-bottom: 0;
        }

        &--description {
          $font-size: 14;
          $line-height: 1.5;
          $lines-to-show: 4;
          font-size: rem-calc($font-size);
          line-height: $line-height;
        }

        /*&--description {
          $font-size: 14;
          $line-height: 1.5;
          $lines-to-show: 4;
          font-size: rem-calc($font-size);
          height: rem-calc($font-size*$line-height*$lines-to-show);
          line-height: $line-height;
          -webkit-line-clamp: $lines-to-show;
        }*/
      }

      @media #{$medium-only} {
        &__content {
