.downloadDocs {
  &.wrapper--narrow {
    @extend .wrapper;
  }

  ul {
    list-style: none;
    margin-left: 0;
    -webkit-text-size-adjust: none; // Webkit (safari ios) fix KBC-801
  }
  .accordion__link {
    background-color: $white;
    border: 1px solid $mediumGrey;
    margin-bottom: rem-calc(25*2/3);
    position: relative;
  }

  .btn-wrapper {
    padding: rem-calc(20*2/3) 0;
    > * {
      margin-bottom: 0;
    }
  }

  &__heading {
    display: table;
    padding-right: rem-calc(45);
    width: 100%;
  }
  &__list {
    margin-top: rem-calc(25*2/3);
  }
  &__title {
    display: table-cell;
    font-size: rem-calc(30*2/3);
    max-width: 70%;
    vertical-align: middle;
    &--sub {
      display: table-cell;
      font-size: rem-calc(20*2/3);
      text-align: right;
      vertical-align: middle;
    }
  }

  &__item {
    background-color: $lightGrey;
    margin-bottom: rem-calc(15*2/3);
    padding: rem-calc(20*2/3);
    display: table;
    width: 100%;

    > * {
      display: table-cell;
    }

    &:last-child {
      margin-bottom: 0;
    }

    &__heading,
    &__date {
      font-size: rem-calc(20*2/3);
    }

    &__date {
      width: rem-calc(80);
      border-right: 1px solid $grey;
      padding-right: rem-calc(20*2/3);
      vertical-align: middle;
    }

    &__info {
      padding-left: rem-calc(20*2/3);
    }

    &__heading {
      font-family: $font-regular;
      color: $darkGrey;
      margin-bottom: rem-calc(15*2/3);
    }

    &__link {
      .icon {
        font-size: rem-calc(30);
        color: $blue;
      }
    }
  }
}

.communiquesDocs {
  &__title {
    font-size: rem-calc(20);
    color: $jet;
    padding-bottom: rem-calc(15);
    margin-bottom: rem-calc(25);
    border-bottom: 1px solid $mediumGrey;
  }

  &__item {
    margin-top: rem-calc(20);
    padding: rem-calc(20);
  }

  &__list {
    li {
      @include display(flex);
      padding-top: rem-calc(20*2/3);

      &:first-child {
        padding-top: 0;
      }
    }

    &__title {
      font-family: $font-regular;
      color: $grey;

      @media #{$small-only} {
        word-break: break-all;
        width: 100%;
      }
    }

    &__link {
      white-space: nowrap;

      span {
        color: $jet;
        vertical-align: middle;
        text-transform: uppercase;
      }

      .icon {
        padding: 0 rem-calc(10);
        font-size: rem-calc(30);
        color: $blue;
      }
    }
  }
}

.b2bDocuments {
  &__heading {
    font-family: $font-regular;
    display: table-cell;
    vertical-align: middle;
  }

  .accordion__link {
    border: none;
  }

  .downloadDocs {
    &__list {
      background: $lightGrey;
    }
    &__item {
      margin-bottom: 0;
      background: transparent;
      &__link {
        vertical-align: middle;
        padding-right: rem-calc(10*2/3);

        .icon {
          font-size: rem-calc(40);
          display: block;
        }
      }
    }
  }
}

@media #{$medium-up} {
  .downloadDocs {
    .accordion__link {
      padding: rem-calc(25);
      .display-status {
        top: rem-calc(30);
        right: rem-calc(25);
      }
    }

    .btn-wrapper {
      padding-top: rem-calc(20);
    }

    &__heading {
      padding: 0 rem-calc(70) 0 rem-calc(15);
    }
    &__title {
      font-size: rem-calc(30);
      &--sub {
        font-size: rem-calc(20);
      }
    }

    &__item {
      padding: rem-calc(15) rem-calc(25);

      &__heading,
      &__date {
        font-size: rem-calc(16);
      }

      &__heading {
        margin-bottom: 0;
        padding-right: rem-calc(40);
      }

      &__info {
        position: relative;
        display: table-cell;
        padding-top: rem-calc(10);
        padding-bottom: rem-calc(10);
        vertical-align: middle;
        .downloadDocs__item__link {
          position: absolute;
          right: 0;
          top: 50%;
          @include transform(translateY(-50%));
        }
      }

      &__date {
        width: rem-calc(100);
        padding-top: rem-calc(10);
        padding-bottom: rem-calc(10);
      }

      &__link {
        > *:hover {
          span {
            color: $blue;
          }
        }
      }
    }
  }

  .communiquesDocs {
    .communiquesDocs__list {
      li {
        padding-top: rem-calc(20);
        &:first-child {
          padding-top: 0;
        }
      }
    }
  }

  .b2bDocuments {
    .accordion__link {
      padding: rem-calc(40) rem-calc(50) rem-calc(40) rem-calc(50);

      .display-status {
        top: rem-calc(40);
        right: rem-calc(50);
      }
    }

    .downloadDocs {
      &__title {
        font-size: rem-calc(25);
      }

      &__heading {
        padding-left: 0;
      }

      &__list {
        background: $white;
        border-top: 1px solid $mediumGrey;
        @include grid-row();
        margin-top: rem-calc(40);
        padding: rem-calc(40) 0 rem-calc(20);
      }

      &__item {
        @include grid-column(
          $columns: 6
        );
        padding-top: rem-calc(5);
        padding-bottom: rem-calc(5);
        padding-left: 0;
      }
    }

  }
}
