.presidentQuote {
  &__content {
    @include display(flex);
    @include align-items(center);
    @include flex-direction(column);
    @include justify-content(center);
  }
  &__img {
    margin-bottom: rem-calc(30*2/3);
    border-radius: 50%;
    width: rem-calc(215*2/3);
    height: rem-calc(215*2/3);
    overflow: hidden;
  }
  &__quote {
    text-align: center;
    blockquote {
      font-size: rem-calc(24*2/3);
      font-style: $paragraph-aside-font-style;
      border: 0;
      position: relative;
      p {
        color: $jet;
      }
      &:before, &:after {
        font-size: rem-calc(79*2/3);
        font-family: $font-bold;
        line-height: 1;
        position: absolute;
        font-style: normal;
      }
      &:before {
        content: '“';
        left: rem-calc(-5);
        top: 0;
      }
      &:after {
        content: '”';
        right: 0;
        bottom: rem-calc(-40*2/3);
      }
    }
  }
  &__cta {
    margin-top: rem-calc(40*2/3);
    .btn-primary {
      width: 100%;
      display: block;
    }
  }
  &__title {
    font-size: rem-calc(22*2/3);
    font-family: $font-bold;
    color: $blue;
  }
  &__sub {
    color: $darkGrey;
    font-size: rem-calc(22*2/3);
    font-family: $font-bold;
    display: block;
  }
}

@media #{$medium-up} {
  .presidentQuote {
    &__content {
      @include flex-flow(row nowrap);
    }
    &__img {
      width: rem-calc(215);
      height: rem-calc(215);
      margin: 0;
    }
    &__quote {
      width: 76%;
      blockquote {
        max-width: 76%;
        margin: 0 auto;
        font-size: rem-calc(24);
      }
      .btn-primary {
        display: inline-block;
        width: auto;
      }
    }
    &__title {
      font-size: rem-calc(16);
      display: inline-block;
      &:after {
        content: '-';
        display: inline-block;
        margin: 0 rem-calc(5);
      }
    }
    &__sub {
      font-size: rem-calc(16);
      display: inline-block;
    }
  }
}
