$navHeight: 50;

.pageHeaderNav {
  position: relative;
  z-index: 1;

  &__list {
    @extend .ul-unstyled;
    @include display(flex);
    @include flex-direction(column);
    width: 100%;
    min-height: rem-calc(80);

    &__item {
      @include display(flex);
      @include align-items(center);
      @include justify-content(center);
      border: 1px solid $mediumGrey;
      border-top: none;
      background: $white;

      a {
        font-size: rem-calc(26*2/3);
        color: $jet;
        display: block;
        width: 100%;
        height: 100%;
        padding: rem-calc(12) rem-calc(10);
        text-align: center;
        @include display(flex);
        @include align-items(center);
        @include justify-content(center);
      }

      &.active,
      .no-touch &:hover,
      .touch &:active,
      &:focus {
        background: $blue;
        border-color: $blue;
        a {
          color: $white;
        }
      }

      .icon {
        padding-right: rem-calc(5);
        font-size: rem-calc(30);
        line-height: rem-calc(17);
        vertical-align: middle;
      }
    }
  }
}

@media #{$small-only} {
  .pageHeaderNav {
    padding: 0;
  }
}

@media #{$medium-up} {
  .pageHeaderNav {
    height: 0;
    &__list {
      @include transform( translateY(-100%) );
      display: table;
      width: 100%;
      table-layout: fixed;
      min-height: initial;

      &__item {
        border: 1px solid $mediumGrey;
        border-left: none;
        display: table-cell;
        vertical-align: middle;
        text-align: center;
        &:first-child, {
          border-left: 1px solid $mediumGrey;

          &.active {
            border-left-color: $blue;
          }
        }
        a {
          font-size: rem-calc(14);
          padding: rem-calc(10) rem-calc(10);
        }
      }
    }
  }
}

