.financeGouvernance {
  &__list {
    margin-top: rem-calc(30*2/3);
  }
  &__item {
    padding: rem-calc(40*2/3) 0;
    border-bottom: 1px solid $grey;
    overflow: hidden;
  }
  &__img {
    margin: 0 auto rem-calc(30*2/3);
    width: rem-calc(215*2/3);
    height: rem-calc(215*2/3);
    overflow: hidden;
  }
  &__content {
    text-align: center;
    &__info {
      text-align: left;
      margin-bottom: rem-calc(20);
      .more-info {
        overflow: hidden;
        @include transition-property(all);
        @include transition-duration(.3s);
        @include transition-timing-function(ease-out);
        min-height: rem-calc(112);
      }
    }
  }
  &__cta {
    a {
      color: $blue;
      font-family: $font-bold;
    }
  }
  &__title {
    font-size: rem-calc(30*2/3);
    font-family: $font-bold;
    color: $blue;
  }
  &__sub {
    color: $darkGrey;
    font-size: rem-calc(24*2/3);
    font-family: $font-bold;
    display: block;
    margin-bottom: rem-calc(40*2/3);
  }
}

@media #{$medium-up} {
  .financeGouvernance {
    &__list {
      margin-top: rem-calc(50);
    }
    &__item {
      padding: rem-calc(50) 0;
      display: table;
      width: 100%;
      &:first-of-type {
        border-top: 1px solid $grey;
      }
    }
    &__content {
      text-align: left;
      display: table-cell;
      vertical-align: middle;
      padding-left: rem-calc(30);
      &__info {
        .more-info {
          min-height: rem-calc(83);
        }
      }
    }
    &__img {
      display: table-cell;
      width: 24.5%;
      height: auto;
      img {
        display: block;
        border-radius: 50%;
        margin: 0 auto;
      }
    }
    &__title {
      font-size: rem-calc(24);
    }
    &__sub {
      font-size: rem-calc(20);
    }
  }
}
