.activities {
  &__heading, &__desc {
    text-align: center;
    margin-bottom: rem-calc(30*2/3);
  }
  &__list {
    margin-top: rem-calc(60*2/3);
  }
  &__item {
    margin-bottom: rem-calc(40*2/3);
    &__title {
      font-size: rem-calc(30*2/3);
      font-family: $font-bold;
      margin: rem-calc(30*2/3) 0;
    }
    &__subs {
      font-family: $font-bold;
      margin-bottom: rem-calc(30*2/3);
    }
  }
}

@media #{$medium-up} {
  .activities {
    &__item {
      @include display(flex);
      @include justify-content(space-between);
      @include flex-flow(row);
      margin-bottom: rem-calc(40);
      &:last-child {
        margin-bottom: 0;
      }
      &:nth-child(odd) {
        @include flex-direction(row-reverse);
      }
      &__title {
        font-size: rem-calc(30);
      }
      &__infos {
        width: 33%;
        padding: 0 rem-calc(30);
      }
    }
    &__heading, &__desc {
      padding: 0 18%;
    }
  }
}
