.groupHomepageArticle {
  .articleList__item {
    @media #{$small-only} {
      padding-bottom: rem-calc(40*2/3);
    }
    .inner {
      background-color: $white;
      border: 1px solid $mediumGrey;
    }

    &__img img {
      display: block;
      width: 100%;
    }

    &:first-child {
      .articleList__item__infos__heading {
        padding-bottom: rem-calc(40*2/3);
        font-size: rem-calc(33*2/3);
      }
    }

    &__infos {
      margin: 0;
      border: 0;

      &__list {
        border-top: 1px solid $mediumGrey;
        padding-top: rem-calc(40*2/3);
        margin: 0 rem-calc(24*2/3);

        @media #{$medium-only} {
          margin: 0 rem-calc(5);
        }

        dt {
          position: relative;
          padding-left: rem-calc(65*2/3);
          margin: 0;
          a {
            font-family: $font-bold;
          }
        }

        dd {
          padding-left: rem-calc(65*2/3);
        }

        .icon {
          position: absolute;
          top: 0;
          left: rem-calc(-8);
          font-size: rem-calc(37);
        }
      }
    }
  }
}
@media #{$medium-up} {
  .groupHomepageArticle {
    .articleList__item {
      height: 100%;

      &:first-child {

        .articleList__item__infos {
          padding-bottom: 0;

          &__list {
            dt {
              padding-left: rem-calc(40);
            }
          }
        }

        .articleList__item__infos__heading {
          padding-top: rem-calc(45);
          margin: 0 rem-calc(16);

          @media #{$medium-only} {
            font-size: rem-calc(20);
            margin: 0 rem-calc(5);
          }
        }
      }

      .articleList__item__infos__heading {
        font-size: rem-calc(22);

        @media #{$medium-only} {
          font-size: rem-calc(20);
        }
      }

      .inner {
        height: 100%;
      }
    }
  }
}
