.noResults {
  ul {
    margin: 0;
    list-style: none;
  }

  [class*="block-grid-"] > li {
    padding-bottom: 0;
  }
  &__heading {
    text-align: center;
    padding: rem-calc(45*2/3) 0;

    p {
      margin: 0;
      font-family: $font-bold;
      color: $jet;
    }
  }

  &__category {
    &__filter {
      text-align: center;
      padding-bottom: rem-calc(20);
      margin-bottom: rem-calc(35);
      border-bottom: 1px solid $mediumGrey;

      dt,
      dd,
      ul,
      nav {
        display: inline-block;
        margin: 0;
        vertical-align: middle;
      }

      dt {
        padding-right: rem-calc(20);
      }

      li {
        padding: 0 rem-calc(5);
      }

      a {
        padding: 0;
        font-family: $font-bold;
        text-transform: uppercase;
        color: $jet;
      }
      .form-dropdown {
        &__content {
          @media #{$small-only} {
            width: 100%;
          }
        }
      }
    }
    &__list {
      dt {
        font-family: $font-bold;
        font-size: rem-calc(32*2/3);
        color: $jet;
        text-transform: uppercase;
      }
      dd {
        a {
          font-family: $font-bold;
          font-size: rem-calc(19*2/3);
          color: $blue;
        }
      }
    }
  }

  dl {
    padding-top: rem-calc(60);
  }

  dl, dd {
    margin-bottom: 0;
  }
}

@media #{$medium-up} {
  .noResults {
    &__heading {
      padding: rem-calc(45) 0;

      h2 {
        font-size: rem-calc(18);
      }
      p {
        font-size: rem-calc(18);
      }
    }

    &__category {
      &__filter {
        dt,
        dd,
        ul,
        li,
        a {
          font-size: rem-calc(18);
        }

        li {
          display: inline-block;
        }

        .form-dropdown {
          min-height: auto;
          margin: 0;

          &__content {
            display: inline-block;
            vertical-align: middle;
          }
        }
      }

      &__list {
        dt {
          font-size: rem-calc(32);
        }
        dd {
          a {
            font-size: rem-calc(19);
          }
        }
      }
    }
  }
}

