.contactMap {

  &__fixed {
    position: fixed;
    top: rem-calc(16) + $topbar-height;

    &.bottom {
      @include align-self(flex-end);
      position: inherit;
      top: inherit;
    }
  }

  .region {
    cursor: default;
    fill: $mapGrey;
    stroke: $white;

    &.enabled {
      cursor: pointer;
      fill: $mediumGrey;
    }
  }

  &__frontier {
    fill: $white;
  }
  
}
