$form-margin-bottom: rem-calc(40*2/3);
$form-margin-bottom-medium-up: rem-calc(24*2/3);
$form-small-text: rem-calc(12);

.login {

  .links {
    @include display(flex);
    margin-bottom: $form-margin-bottom;
  }

  fieldset {
    input:last-child {
      margin-bottom: $form-margin-bottom;
    }
  }

  input {
    margin-bottom: rem-calc(16);
  }

  &--save-password,
  &--password-link {
    @include flex(1);
  }

  &--save-password {
    display: inline-block;

    .label-checkbox {
      @include align-items(center);
      padding: 0;
    }

    .label-checkbox:before {
      margin-right: rem-calc(10);
    }
  }

  &--password-link {
    text-align: right;
  }

  button {
    width: 100%;
  }

  @media #{$medium-up} {
    min-width: rem-calc(320);

    .heading {
      padding-top: 0;
    }

    .heading,
    .links,
    fieldset input:last-child {
      margin-bottom: $form-margin-bottom-medium-up;
    }

    input {
      margin-bottom: rem-calc(8);
    }
  }
}

.header__menu .main-menu__item .login {
 
  &--save-password .label-checkbox,
  a.login--password-link {
    line-height: inherit;
    
    @media #{$medium-up} {
      font-size: $form-small-text;
    }
  }
  
  a.login--password-link {
    color: $grey;
    &:hover {
      color: $blue;
    }
  }
} 


.header .f-dropdown {
  &.login-form {
    max-width: rem-calc(440);
    padding: rem-calc(28);
    width: auto;
  }
}

.reveal-modal .login {
  .heading {
    padding-top: 0;
  }
}
