// make website readable on IE8 and IE9
.oldie {

  .header,
  .sharing {
    @extend .clearfix;
  }

  // site header
  .header {
    &__title {
      float: left;
    }

    &__menu {
      float: right;

      .main-menu {
        &__item {
          float: left;
        }
      }
    }
  }

  // pageHeaderNav
  .pageHeaderNav {
    &__list {
      display: table;
      width: 100%;
      &__item {
        display: table-cell;
        vertical-align: middle;
      }
    }
  }

  // sharing
  .sharing {
    &__items {
      &__item {
        float: left;
      }
    }
  }


  .homeDocs__main__link {
    display: block;
    > * {
      width: 100% !important;
    }
  }


  // search results
  .submit-wrapper {
    display: table;
    > * {
      display: table-cell !important;
    }
  }

  .searchResultsItem__infos__details {
    .details {
      @extend .clearfix;

      > li {
        float: left;
      }
    }
  }

  .results-accordion__trigger {
    display: table;
    width: 100%;

    > * {
      display: table-cell;
      vertical-align: middle;
    }
  }


  // groupe pages
  .actionnaireCarnet__listInfor__list {
    @extend .clearfix;

    dt {
      float: left;
    }

    dd {
      float: right;
    }
  }

}