.latestJobs {
  &__item {
    &__date,
    &__title,
    &__address {
      line-height: rem-calc(32);
    }

    &__heading {
      font-family: $font-bold;
      margin-bottom: rem-calc(30*2/3);
    }
    &__date {
      font-size: rem-calc(30*2/3);
      color: $blue;
    }
    &__title {
      font-size: rem-calc(30*2/3);
    }
    &__address {
      font-size: rem-calc(24*2/3);
    }
    &__content {
      margin-bottom: rem-calc(20);
      span {
        display: block;
      }
    }
  }
}

@media #{$small-only} {
  .latestJobs {
    .btn-wrapper {
      .btn-primary {
        width: 100%;
        display: block;
      }
    }
  }
}

@media #{$medium-up} {
  .latestJobs {
    &__list {
      @include grid-row($behavior: nest);
    }
    &__item {
      &__heading {
        margin-bottom: rem-calc(5);
      }
      &__content {
        padding-right: rem-calc(40);
        p {
          font-size: rem-calc(14);
        }
      }
    }
  }
}
