$counselor-size: rem-calc(60);

.contact {
  .heading__title--sub {
    @media #{$medium-up} {
      max-width: rem-calc(658);
    }
  }
}

.contacts {
  @media #{$medium-up} {
    @include display(flex);
    @include align-items(flex-start);
  }

  &__list {
    padding: 0;

    @media #{$medium-up} {
      @include moz-fix-flexbox-shrink();
      @include flex(2);
    }

    > li {
      background: $white;
      padding: rem-calc(16);
      margin-bottom: rem-calc(16);

      &:last-child {
        margin-bottom: 0;
      }
    }

    .accordion__link {
      padding: 0;
      margin: 0;
      .display-status {
        right: 0;
      }

      &.active {
        margin-bottom: rem-calc(16);
      }
    }

    article {
      padding: rem-calc(22) rem-calc(14);
      background: $lightGrey;
    }
  }

  &__address {
    @include display(flex);
    @include align-items(stretch);
    @include justify-content(space-between);
    @include flex-flow(column nowrap);

    @media #{$medium-up} {
      @include flex-flow(row nowrap);
    }

    > * {
      @include flex(1);
      @include moz-fix-flexbox-shrink();
    }

    &__content {
      margin-top: rem-calc(16);

      @media #{$medium-up} {
        margin-top: 0;
        margin-left: rem-calc(16);
      }

      .heading__title {
        margin-bottom: rem-calc(8);
      }

      p {
        margin: 0;
      }
    }
  }

  &__carte {
    display: none;

    @media #{$medium-up} {
      @include align-self(stretch);

      &-wrapper {
        background-color: $white;
        width: 100%;
      }

      .contactMap {
        width: 100%;
        padding: rem-calc(48) rem-calc(16);

        @media #{$medium-up} {
          min-height: rem-calc(400);
        }

        @media #{$large-up} {
          min-height: rem-calc(450);
        }
      }
      @include display(flex);
      @include align-items(flex-start);
      @include flex(1);
      background-repeat: no-repeat;
      background-position: top;
      background-size: contain;
      background-clip: content-box;
      margin-right: rem-calc(16);
    }
  }
}

.counselors {
  list-style: none;
  margin: 0 0 rem-calc(16);

  li {
    background-repeat: no-repeat;
    background-position: left center;
    background-size: $counselor-size;
    padding: rem-calc(16) 0 rem-calc(16) $counselor-size + rem-calc(16);
    border-bottom: 1px solid $mediumGrey;
  }

  a {
    color: $grey;
    display: block;
    position: relative;
    padding-left: rem-calc(32);

    .icon {
      color: $jet;
      position: absolute;
      left: 0;
      font-size: rem-calc(28);
      line-height: rem-calc(28);
      top: 0;
    }

    &.mail {
      font-weight: bold;
    }

    &.phone {
      .icon {
        @include transform(rotate(45deg));
      }
    }
  }

}
