.editorialContentArticle {
  &.bg-lightGrey {
    padding: rem-calc(20*2/3);
  }

  * {
    line-height: 1.3;
  }

  p,
  .editorialContentArticle__content__hightlight,
  ul,
  ol,
  .editorialContentArticle__figure {
    margin-bottom: rem-calc(30*2/3);
    font-size: inherit;
  }

  sub,
  sup {
    font-size: 70%;
  }
  sub {
    bottom: - rem-calc(0.3);
  }
  sup {
    top: - rem-calc(0.6);
  }

  h1, h2, h3, h4, h5, h6 {
    margin-bottom: rem-calc(45*2/3);
    font-family: $font-bold;
  }
  h1 {
    font-size: rem-calc(25*2/3 + 30);
  }
  h2 {
    font-size: rem-calc(25*2/3 + 20);
  }
  h3 {
    font-size: rem-calc(25*2/3 + 15);
  }
  h4 {
    font-size: rem-calc(25*2/3 + 8);
  }
  h5 {
    font-size: rem-calc(25*2/3 + 3);
  }
  h6 {
    font-size: rem-calc(25*2/3);
  }

  ul,
  ol {
    list-style: inherit;
    list-style-position: inside;
    color: $grey;
    margin-left: rem-calc(10);
  }

  ol {
    list-style-type: decimal;
  }

  .overflow {
    overflow: auto;
  }

  .editorialContentArticle__content__hightlight {
    font-family: $font-bold;
    font-weight: $font-weight-normal;
    color: $grey;
  }

  blockquote {
    border: 0;
    padding: 0;
    margin: rem-calc(90*2/3) 0 rem-calc(45*2/3);
    position: relative;
    &:before {
      content: '“';
      font-size: rem-calc(126*2/3);
      font-family: $font-bold;
      font-weight: $font-weight-normal;
      line-height: 1;
      color: $blue;
      position: absolute;
      top: - rem-calc(60*2/3);
      left: 0;
      opacity: .2;
    }
    p {
      color: $blue;
      font-size: rem-calc(36*2/3);
      font-family: $font-thin;
      font-weight: $font-weight-normal;
    }
  }

  table[border="1"] {
    td {
      border: 1px solid $mediumGrey;
    }
  }
}

@media #{$medium-up} {
  .editorialContentArticle {
    &.bg-lightGrey {
      padding: rem-calc(45);
      margin-bottom: rem-calc(40);
    }

    * {
      line-height: 1.5;
    }

    p,
    .editorialContentArticle__content__hightlight,
    ul,
    ol,
    .editorialContentArticle__figure {
      margin-bottom: rem-calc(30);
    }

    &__figure {
      width: 49%;
      float: right;
    }

    &__content {
      width: 48%;
      float: left;
    }

    p,
    ul,
    ol {
      font-size: rem-calc(16);
    }

    &--big {
      p,
      ul,
      ol {
        font-size: rem-calc(20);
      }
    }

    .editorialContentArticle__content__hightlight {
      font-size: rem-calc(20);
      margin-bottom: rem-calc(50);
    }

    blockquote {
      margin: rem-calc(80) 0 rem-calc(60);
      &:before {
        font-size: rem-calc(80);
      }
      p {
        font-size: rem-calc(30);
      }
    }
  }
}
