$border-left-width: rem-calc(6);

@mixin row-status($color) {
  @if $color != $grey {
    td {
      &:first-child {
        border-left-color: $color;
      }
    }
  }

  td {
    .colored-label {
      @include display(flex);
      @include align-items(center);
      color: $color;
      &:before {
        display: inline-block;
        font-size: rem-calc(24);
        margin-right: rem-calc(8)
      }
    }
  }
}

.outilsTable {
  border: 0;
  border-collapse: collapse;
  width: 100%;

  .caps {
    text-transform: uppercase;
  }

  .btn-white,
  .btn-primary,
  .btn-orange,
  p,
  ul {
    margin: 0;
  }

  .btn-white,
  .btn-primary,
  .btn-orange {
    padding: rem-calc(8) rem-calc(10);
  }

  ul {
    list-style: none;
  }

  th,
  td {
    border: 0;
  }

  thead {
    background: $white;

    &:after {
      content: '';
      display: block;
      height: rem-calc(32);
      width: 100%;
    }

    tr {
      border-bottom: 3px solid $blue;
    }

  }

  th {
    border-right: 1px solid $blue;
    font-family: $font-bold;
    font-size: rem-calc(12);
    font-weight: normal;
    padding: rem-calc(10) rem-calc(20);
    position: relative;
    white-space: nowrap;

    strong {
      color: $alert-color;
      font-family: $font-regular;
    }

    &:last-child {
      border-right: 0;
    }

    &.col-large {
      width: 18%;
    }
  }

  .tbodyhead {

    &__btn {
      border-radius: rem-calc(3);
      font-size: rem-calc(14);
      margin-bottom: 0;
      padding: rem-calc(10) rem-calc(20);
    }

    th {
      background-color: $darkBlue;
      border-left-color: $darkBlue;
      border-left-style: solid;
      border-left-width: $border-left-width;
      padding: rem-calc(10) rem-calc(15);
    }

    &__wrapper {
      @include display(flex);
      @include align-items(center);
      width: 100%;

      .fl_r {
        margin-left: auto;
      }

      &:before {
        color: $white;
        font-size: rem-calc(32);
        margin-right: rem-calc(16);
      }
    }

    &__title {
      @include display(flex);
      @include align-items(center);
      &__head {
        color: $white;
        font-family: $font-bold;
        font-size: rem-calc(16);
        margin-right: rem-calc(20);

        .light {
          font-family: $font-regular;
          font-size: rem-calc(16);
          font-weight: $font-weight-normal;
        }
      }
    }

    &__actions {
      @include display(flex);
      @include align-items(center);

      .label-checkbox {
        margin-left: rem-calc(20);
      }

      > button,
      > a {
        margin-right: rem-calc(10);

        &.btn-icon--only {
          height: auto;
          padding: rem-calc(3);
          width: auto;

          .icon:before {
            color: $jet;
            font-size: rem-calc(28);
          }
        }

        &:last-child {
          margin-right: 0;
        }
      }
    }
  }

  tbody {
    &:after {
      content: '';
      display: block;
      height: rem-calc(40);
      width: 100%;
    }

    //tr:nth-child(even),
    .even {
      background-color: transparent;

      td {
        border-left-color: transparent;
      }
    }

    //tr:nth-child(odd):not(:first-child),
    .odd {
      background-color: $lightGrey;

      td {
        border-left-color: $lightGrey;
      }
    }

    td {
      padding: rem-calc(20) rem-calc(15);
    }

    td:first-child {
      border-left-style: solid;
      border-left-width: $border-left-width;
    }

    .outilsTableRow {
      .pinel {
        color: $pink;
      }

      &__title,
      &__type,
      &__price {
        > * {
          font-size: rem-calc(14);
          color: $jet;
        }
        .tag {
          color: $white;
          font-size: rem-calc(11);
          margin-bottom: rem-calc(8);
          &:last-child {
            margin-bottom: 0;
          }
        }
      }

      &__title {
         .header-name {
          font-family: $font-bold;
          color: $blue;

          span {
            color: $jet;
            display: block;
          }
        }

        > :nth-child(2) {
          margin-top: rem-calc(8);
        }
      }

      &__price {
        li {
          white-space: nowrap;
          span {
            display: inline-block;
            font-family: $font-bold;
            text-align: right;
            width: rem-calc(75);
          }
          .tva {
            text-align: center;
            width: rem-calc(60);
          }
        }
      }

      &__loyer,
      &__rentabilite {
        text-align: center;
      }

      &__actions {

        > * {
          @include display(flex);
          @include justify-content(flex-end);
          @include align-items(center);
        }

        a,
        button {
          font-size: rem-calc(12);
          padding-left: rem-calc(5);
          padding-right: rem-calc(5);
          width: rem-calc(115);
        }

        .label-checkbox {
          margin-left: rem-calc(20);
        }
      }
    }

    .tva {
      font-size: 0.7rem;
      margin-left: rem-calc(8);
      padding: rem-calc(4) 0;
    }

    li {
      .tva {
        margin-bottom: rem-calc(4);
      }
      &:last-child {
        .tva {
          margin-bottom: 0;
        }
      }
    }

  }

  .outilsTableLink {
    td {
      background: $white;
      border-left-color: $white;
      text-align: right;

      a {
        @include display(inline-flex);
        color: $jet;
        font-family: $font-bold;
        font-size: rem-calc(16);
        text-align: right;
        .icon {
          line-height: inherit;
        }

        .icon-arrow:before {
          display: inline;
        }

        .icon-planing {
          font-size: rem-calc(50);
        }
      }
    }
  }

  .fl_r {
    text-align: right;
  }

  .label-checkbox:before {
    margin: 0 auto;
  }

  .available {
    @include row-status($flashyGreen);
  }

  .option {
    @include row-status($orange);
  }

  .confirmed {
    @include row-status($blue);
  }

  .not-available {
    @include row-status($grey);

    &.outilsTableRow td {
      .pinel,
      .tva,
      > *,
      > * > * {
        color: $grey !important;
      }

      .tva {
        border-color: $grey;
      }
    }

  }
}

.outilsTable tbody tr.innerRow{
  td {
    padding: 0;
  }


  .expand-area {
    display: none;
    padding: $border-left-width $border-left-width $border-left-width 0;
  }

  table {
    background-color: $evenMoreLightGrey;
    border: 0;
    margin: 0;
    padding: rem-calc(20) rem-calc(15);
    width: 100%;

    tbody {
      &:after {
        height: initial;
      }
    }

    td {
      padding: 0;
      border-left-width: 0;
    }

    ul {
      font-size: rem-calc(14);
    }
  }
}

.no-touch .outils-actions .display-status:hover {
  background-color: $white;
}

.outils-actions {
  @include display(flex);
  @include justify-content(space-between);
  @include align-items(stretch);

  &.form-dropdown.form-dropdown--responsive {
    .outils-actions__filter.form-dropdown__content,
    .outils-actions__form {
      @include display(flex);
      @include flex-flow(column nowrap);
      @include justify-content(space-between);
    }

    .outils-actions__filter.form-dropdown__content {
      .heading__title {
        display: block;
      }

      .buttons {
        @include justify-content(flex-start);

        a {
          padding-left: rem-calc(20);
          padding-right: rem-calc(20);
          line-height: rem-calc(24);
        }
      }
    }
  }

  margin-bottom: rem-calc(40);

  > div * {
    margin-bottom: 0;
  }

  &__form,
  &__button {
    @include flex(3);
  }

  &__filter {
    @include flex(5);
  }

  &__button {
    text-align: right;
    @include align-self(flex-end);

    .display-status {
      background-color: $jet;
      border: 0;
      padding-left: rem-calc(32);
      @include transition-property(background-color);
      @include transition-duration(.3s);
      @include transition-timing-function(ease-in-out);

      &:before,
      &:after {
        background-color: $white;
        width: rem-calc(16);
        @include transition-property(background-color);
        @include transition-duration(.3s);
        @include transition-timing-function(ease-in-out);
      }
    }
  }

  .heading__title {
    font-size: rem-calc(16);
    margin-bottom: rem-calc(16);
  }

  .searchBasicInput {
    margin-bottom: 0;
    width: 80%;

    input[type=search] {
      @include flex(5);

      font-size: 1rem;
      max-height: 2.8125rem;
    }

    .button-submit .icon-search {
      font-size: 1.2rem;
    }
  }

  .btn-filter {
    font-size: 0.8125rem;
  }
}
