.graphicPresentation {
  &__list {
    @include grid-row($behavior: nest);
  }
  &__item {
    margin-bottom: rem-calc(60*2/3);
    &__heading {
      font-size: rem-calc(24*2/3);
      font-family: $font-bold;
    }
    .desc {
      font-size: rem-calc(18*2/3);
      color: $jet;
    }
  }
}

@media #{$medium-up} {
  .graphicPresentation {
    &__list {
      &:first-of-type {
        padding-top: rem-calc(40);
      }
    }
    &__item {
      @include display(flex);
      @include flex-direction(column);
      margin-bottom: rem-calc(60);
      &__heading {
        font-size: rem-calc(24);
      }
      .desc {
        font-size: rem-calc(18);
        margin-bottom: rem-calc(35);
      }
      &:nth-child(2n+1) {
        clear: left;
      }
      &:last-of-type {
        margin-bottom: 0;
      }
      &__img {
        margin-top: auto;
      }
    }
  }
}
