.bienHeader {
  .toolbox__intro {
    border-bottom: none;
    text-align: left;
    &--border {
      padding: rem-calc(20) 0;
      margin-top: rem-calc(20);
      border: 1px solid $mediumGrey;
      border-left: none;
      border-right: none;
    }
  }

  .heading__title.smaller {
    font-size: rem-calc(40*2/3);

    &.heading__title--sub {
      font-size: rem-calc(32*2/3);
    }
  }

  .content-price {
    &.bienPrice {
      text-align: left;
      padding-bottom: 0;
      border-bottom: none;
    }
  }

  .toolsList {
    padding: rem-calc(20) 0 0;
    border: 1px solid $mediumGrey;
    border-left: none;
    border-right: none;
  }
  .phone-green {
    margin: rem-calc(35*2/3) 0;
  }
}

@media #{$medium-up} {
  .bienHeader {
    .slick-slider .slick-prev,
    .slick-slider .slick-next {
      @include transform(translateY(-100%));
    }

    .heading__title.smaller {
      font-size: rem-calc(30);
    }
    .heading--bordered:before {
      width: rem-calc(30);
      height: rem-calc(4);
    }

    .toolbox__intro {
      padding: 0;
      &--border {
        padding: rem-calc(20) 0;
      }
    }
    .programHeader__content {
      top: rem-calc(-20);

      &:before {
        content: "";
        @include position(absolute, 0 0 0 0);
        background: $lightGrey;
        box-shadow: 0 0 0 rem-calc(20) $lightGrey;
        z-index: -1;
        display: block;
      }

      .toolbox,
      &__details {
        position: relative;
        padding: rem-calc(20);
        padding-top: rem-calc(30);
        background-color: $white;
      }

      .toolbox {
        width: 36%;
        padding-bottom: 0;
      }

      &__details {
        width: 62%;
        float: right;
        height: 100%;
        margin-left: 0 !important;
        @include display(flex);
        @include flex-direction(column);
      }
    }
    .contactUs-mini {
      margin-top: auto;
      > a {
        text-align: center;
      }

      .contactUs__cta {
        > a {
          margin-bottom: 0;
        }
      }
    }
  }
}

@media #{$medium-only} {
  .bienHeader {
    margin-bottom: rem-calc(10);
    .heading__title.smaller {
      font-size: rem-calc(24);
    }
    .programHeader__content {
      width: 96%;
      margin: 0 auto;

      .toolbox {
        width: 37%;
      }

      &__details {
        width: 61%;
      }
    }
    .contactUs-mini > a {
      margin-bottom: rem-calc(15);
    }
  }
}
@media #{$small-only} {
  .bienHeader {
    .toolbox__intro {
      border-bottom: 1px solid $mediumGrey;
    }
    .sharing {
      width: 100%;
    }
    .toolsList li {
      padding-right: rem-calc(5);
      a {
        display: block;
        height: auto;
        min-height: 40px;
        padding-left: rem-calc(45);
        .icon {
          @include position(absolute, 50% null null 0);
          margin-top: -22px;
        }
        .text {
          padding-left: 0;
        }
      }
    }
  }
}
