.headerB2B {
  &.bienHeader .heading__title--sub {
    padding-top: rem-calc(25*2/3);
  }
  .heading--inline {
    margin-bottom: rem-calc(10*2/3);
    .heading__title {
      display: inline-block;
      margin-right: rem-calc(10);
    }
    .heading__title--sub {
      padding-top: 0;
      font-size: rem-calc(40*2/3);
    }
  }
  .toolbox__content {
    padding-top: rem-calc(30*2/3);
    margin-top: rem-calc(10);
    border-top: 1px solid $mediumGrey;
    .toolbox__heading {
      font-size: rem-calc(20*2/3);
    }
    .content-price {
      text-align: left;
      .text {
        display: inline-block;
        vertical-align: middle;
        margin-right: rem-calc(20*2/3);
      }
      .tags {
        @include display(inline-block);
      }
    }
    .programHeader__download {
      width: 100%;
      display: block;
      margin-top: rem-calc(30*2/3);
      font-size: rem-calc(30*2/3);
    }
  }

  .programHeaderFigureItem {
    position: relative;
    .btn-wrapper {
      position: absolute;
      top: rem-calc(20);
      .tag {
        top: 0;
        font-size: rem-calc(12);
      }
    }
  }
}

@media #{$medium-up} {
  .headerB2B {

    .content-price {
      &__item {
        @include display(flex);
        @include flex-direction(row);

        .tags {
          margin-left: rem-calc(20);
        }
      }
    }

    &.bienHeader .toolbox {
      @include grid-column(4);
      padding-left: 0;

      .programContactInformation {
        margin: 0;
        padding: 0;
        background-color: $white;
        &__heading {
          padding: rem-calc(20) 0 0;
          text-align: left;
          color: $grey;
        }
        &__item {
          position: relative;
          padding-left: 0;
          padding-right: 0;
          margin-top: rem-calc(5);

          .text-bold,
          > a {
            display: block;
            position: relative;
            padding-left: rem-calc(40);
            &:before {
              display: none;
            }
          }
          .icon {
            position: absolute;
            top: 0;
            left: rem-calc(10);
          }
          &:first-child {
            .text-bold,
            > a {
              padding-left: rem-calc(30);
            }
            .icon {
              left: 0;
            }
          }
        }
        &__phone {
          .text {
            line-height: rem-calc(22);
          }
        }
      }

      .toolsList {
        border-top: 1px solid $mediumGrey;
        margin: 0;
      }

      .sharing {
        border-top: 1px solid $mediumGrey;
      }
    }

    .toolbox {
      padding-top: rem-calc(30);

      .bienPrice {
        padding-bottom: rem-calc(30);
      }

      .toolsList {
        padding-top: rem-calc(30);

        li {
          width: 50%;
          padding: 0 rem-calc(5);

          &:first-child {
            padding-left: 0;
          }
          &:last-child {
            padding-right: 0;
          }
        }
      }
    }

    .programHeader__content {
      top: rem-calc(20);
      &:before {
        display: none;
      }
      &__details {
        padding-top: rem-calc(30);
      }
    }

    &.bienHeader .programHeader__content__details {
      @include grid-column(
        $columns: 7,
        $offset: 1
      );
      padding-right: 0;
    }

    .programDocumentDownload {
      padding-top: rem-calc(60);
      &__heading {
        width: 100%;
        padding: 0;
      }
      &__items {
        width: 100%;
        padding: 0;
      }
      .heading__title {
        font-size: rem-calc(20);
      }
      .heading--small .heading__title {
        font-size: rem-calc(13);
      }
      .btn-wrapper {
        text-align: left;
      }
      &__items {
        margin-bottom: rem-calc(20);
      }
    }

    .btn-poser {
      width: 100%;
    }

    .heading--inline {
      .heading__title {
        display: block;
        margin-right: 0;
      }
      .heading__title--sub {
        font-size: rem-calc(40);
      }
    }

    .toolbox__content {
      .toolbox__heading {
        font-size: rem-calc(20);
      }
      .content-price {
        .text {
          display: block;
        }
      }
      .programHeader__download {
        margin-bottom: 0;
      }
    }

    .programHeaderFigureItem {
      .btn-wrapper {
        top: rem-calc(60);
        .tag {
          font-size: rem-calc(19);
        }
      }
    }

    .header__menu .main-menu__item {
      padding-left: rem-calc(16);
    }

    .main-title img {
      width: 80%;
      position: static;
      top: 0;
    }
  }
}

@media #{$medium-only} {
  .headerB2B {
    .programDocumentDownload {
      padding-top: rem-calc(30);
    }
  }
}

@media #{$small-only} {
  .headerB2B {
    &.bienHeader .heading {
      margin-bottom: rem-calc(25*2/3);
    }
    .toolsList {
      margin: 0;
      border-bottom: none;

      a {
        padding: rem-calc(5) rem-calc(5) rem-calc(5) rem-calc(45);
        border: 1px solid $mediumGrey;
      }

      li:last-child {
        padding: 0;
      }
    }
    .bienPrice {
      overflow: hidden;
      padding-top: rem-calc(25*2/3);
      border-top: 1px solid $mediumGrey;
      @include display(flex);
      @include justify-content(space-between);
      @include align-items(center);
      @include flex-flow(row wrap);

      .content-price__item {
        @include display(flex);
        @include justify-content(space-between);
        @include flex(no-wrap);
        @include flex(row);
        max-width: 49%;
        &:last-child {
          margin-bottom: rem-calc(25*2/3);
        }

        .tags {
          margin-left: rem-calc(20*2/3);
        }
      }
      .tva {
        white-space: nowrap;
      }
    }
    .programHeader__content__details {
      .characteristicList {
        padding-top: 0;
      }
    }
  }
}
