.metier {
  &__list {
    margin-bottom: rem-calc(40*2/3);
  }
  &__item {
    background-color: $white;
    margin-bottom: rem-calc(30*2/3);
    &__heading {
      padding: rem-calc(20*2/3) rem-calc(30*2/3);
      border-bottom: 1px solid $lightGrey;
    }
    &__content {
      padding: rem-calc(30*2/3);
      p:last-of-type {
        margin-bottom: 0;
      }
      span {
        font-family: $font-bold;
      }
    }
    &__title {
      color: $blue;
      font-family: $font-bold;
    }
  }
}

@media #{$medium-up} {
  .metier {
    &__item {
      display: table;
      width: 100%;
      &__heading {
        width: rem-calc(280);
        padding: rem-calc(30) rem-calc(40);
        display: table-cell;
        border-bottom: 0;
        border-right: 1px solid $lightGrey;
      }
      &__title {
        font-size: rem-calc(24);
      }
      &__content {
        padding: rem-calc(30) rem-calc(50);
        display: table-cell;
      }
    }
  }
}
