.dashboard {
  &__wrapper {
    @include grid-row($behavior: nest);
    .columns {
      overflow: hidden;
      padding-bottom: rem-calc(40*2/3);

      .heading {
        @include display(flex);
        @include flex-wrap(nowrap);
        @include justify-content(center);
        position: relative;
        margin: 0;
        padding-bottom: rem-calc(25);
        text-align: center;
        @media #{$small-only} {
          padding-top: rem-calc(25);
        }
        &__title {
          @include align-self(flex-end);
          font-size: rem-calc(60*2/3);
          line-height: rem-calc(30);

          &--sub {
            @include align-self(flex-end);
            padding-left: rem-calc(20*2/3);
            font-size: rem-calc(25*2/3);
            line-height: rem-calc(12);
          }
        }
      }
    }
  }

  header.heading {
    text-align: center;

    .heading__title {
      font-size: rem-calc(30*2/3);
    }
  }

  &__calendar,
  &__selection,
  &__grande {
    position: relative;
    background-color: $white;
  }

  &__calendar {
    overflow: hidden;
    &__item {

      caption {
        padding: rem-calc(40) 0;
        font-size: rem-calc(25);
      }

      .slick-arrow {
        top: rem-calc(25);
        @include transform(translateY(0));
        background-color: transparent;
        border: 0;
        @media #{$medium-up} {
          top: rem-calc(40);
        }
        @media #{$medium-only} {
          @media #{$portrait} {
            top: rem-calc(35);
          }
        }

        &:focus {
          background-color: transparent;
          box-shadow: none;
        }

        &.slick-prev,
        &.slick-next {
          &:hover {
            background-color: transparent;
          }
          .icon {
            border-top-color: $blue;
            border-right-color: $blue;
          }
        }
        &.slick-prev {
          left: rem-calc(10);
          @media #{$small-only} {
            left: rem-calc(25);
          }
        }
        &.slick-next {
          right: rem-calc(10);
          @media #{$small-only} {
            right: rem-calc(25);
          }
        }
      }
      .slick-slide {
        @media #{$small-only} {
          padding: 0 rem-calc(15);
        }
      }
      table {
        width: 100%;
        border: 0;
        table-layout: fixed;

        th {
          font-family: $font-bold;
          line-height: rem-calc(35);
          text-align: center;
        }
        td {
          position: relative;
          font-family: $font-bold;
          text-align: center;
          font-size: rem-calc(17);
          line-height: rem-calc(33);
          border-bottom: 1px solid $lightGrey;
          &:nth-child(even) {
            color: $mediumGrey;
          }
          &:nth-child(odd) {
            color: $grey;
          }
          sup {
            display: block;
            position: absolute;
            top: rem-calc(2);
            right: 0;
            width: rem-calc(19);
            height: rem-calc(19);
            background-color: $blue;
            border-radius: 50%;
            line-height: rem-calc(19);
            color: $white;
          }
        }

        @media #{$medium-only} {
          th {
            padding: rem-calc(3);
          }
          td {
            font-size: rem-calc(15);
            sup {
              width: rem-calc(16);
              height: rem-calc(16);
              line-height: rem-calc(16);
            }
          }
        }
      }
    }

    &__event {
      &__title {
        font-family: $font-bold;
        font-size: rem-calc(17);
        color: $jet;
        text-transform: uppercase;
      }

      &__item {
        @include display(flex);
        padding-top: rem-calc(20);
        margin-top: rem-calc(15);
        border-top: 1px solid $lightGrey;
        @media #{$medium-only} {
          padding-left: rem-calc(1);
        }

        &:first-child {
          padding-top: 0;
          margin-top: 0;
          border-top: none;
        }

        dt {
          padding-right: rem-calc(20);
        }

        .date {
          @include display(flex);
          @include flex-flow(column nowrap);
          @include justify-content(center);
          width: rem-calc(82);
          height: rem-calc(82);
          margin: 0;
          font-family: $font-bold;
          background-color: $blue;
          text-align: center;
          color: $white;
          .day {
            font-size: rem-calc(40);
            line-height: 1;
          }
          .month {
            font-size: rem-calc(12);
          }
        }

        dd {
          @include flex-grow(1);
          ul {
            margin: 0;
            list-style: none;
          }
          li {
            padding-top: rem-calc(15);
            margin-top: rem-calc(20);
            border-top: 1px solid $lightGrey;
            &:first-child {
              padding-top: 0;
              margin-top: rem-calc(-4);
              border: none;
            }
          }
          p {
            margin: 0;
            font-size: rem-calc(14);
          }
        }

        &--title {
          a {
            display: block;
            font-family: $font-bold;

            &:first-child {
              color: $jet;
            }
          }
        }
      }
    }
  }

  &__selection {
    padding-bottom: 0;
  }

  .accordion {
    padding: 0;

    &__link {
      background-color: transparent;
      position: absolute;
      right: 0;
      bottom: rem-calc(25);
      padding: 0;
      margin: 0;

      .display-status {
        bottom: 0;
      }
    }
  }

  &__list {
    margin: rem-calc(25) 0 0;
    list-style: none;

    li {
      @include display(flex);
      @include flex-wrap(nowrap);
      padding: rem-calc(40*2/3) rem-calc(20*2/3);
      border-top: 1px solid $lightGrey;
    }
    &__img {
      @include flex(2);
      padding-right: rem-calc(30*2/3);
    }
    &__content {
      @include flex(3);

      p {
        margin: 0;
        font-size: rem-calc(14);

        &:last-child {
          padding-bottom: rem-calc(15*2/3);
        }
      }

      &--title {
        a {
          display: block;
          padding-bottom: rem-calc(8*2/3);
          font-size: rem-calc(14);
          font-family: $font-bold;
          color: $jet;
          &.city {
            color: $blue;
          }
        }
      }
      &--description {
        padding-bottom: rem-calc(15*2/3);
      }
    }
  }

  .btn-primary {
    margin: 0;
  }

  .seemore-link {
    padding-top: rem-calc(20);

    a {
      display: block;
      width: 100%;
      margin: 0;
      border-radius: 0;
    }
  }
}
@media #{$medium-up} {
  .dashboard {
    .columns {
      .heading {
        &__title {
          font-size: rem-calc(60);

          &--sub {
            padding: rem-calc(10) 0 0;
            font-size: rem-calc(17);
            line-height: rem-calc(17);
          }
        }
      }
    }
    header.heading {
      .heading__title {
        font-size: rem-calc(30);
      }
    }

    &__calendar,
    &__selection,
    &__grande {
      @include display(flex);
      @include flex-flow(column);
      // max-height: rem-calc(900);
      padding: 0 rem-calc(20);
    }

    &__grande {
      .dashboard__list__content--title a {
        display: block;

        &.city {
          margin-left: 0;
        }
      }
    }

    &__calendar {
      &__item {
        caption {
          padding: rem-calc(55) 0;
        }
      }
      .slick-slider {
        margin: 0;
      }
    }

    .columns {
      .heading {
        display: block;
        padding-top: rem-calc(37);

        &__title {
          padding-bottom: rem-calc(10);

          &--sub {
            padding-bottom: 0;
          }
        }
      }
    }
    &__list {
      margin-top: 0;
      overflow-y: auto;

      li {
        padding-left: 0;
        padding-right: 0;
      }

      &__img {
        @include flex(2);
        padding-left: 0;
      }
      &__content {
        @include flex(5);
        padding-right: 0;

        &--title {
          a {
            display: inline;
            line-height: rem-calc(16);

            &.city {
              margin-left: rem-calc(5);
            }
          }
        }

        &--description {
          padding-bottom: rem-calc(12);
        }
      }
    }

    .seemore-link {
      margin: auto rem-calc(-20) 0;
    }
  }
}
