.typeLogements {
  ul {
    margin: 0;
    list-style: none;
  }

  &__heading {
    padding: rem-calc(20*2/3) 0 rem-calc(15*2/3);
    text-align: center;

    h3 {
      font-family: $font-bold;
      font-size: rem-calc(24*2/3);
      color: $jet;
    }
    p {
      margin-top: rem-calc(25*2/3);
    }
  }

  &__list {
    padding: rem-calc(40*2/3) rem-calc(35*2/3);
    margin-top: rem-calc(50);

    dt {
      font-family: $font-bold;
      font-size: rem-calc(30*2/3);
    }
    dd {
      margin: 0;
      padding-top: rem-calc(20*2/3);
    }
    li {
      margin-top: rem-calc(10);
    }
    a {
      font-family: $font-bold;
    }
  }
}

@media #{$medium-up} {
  .typeLogements {
    &__heading {
      padding: rem-calc(20) 0 rem-calc(15);

      h3 {
        font-size: rem-calc(24);
      }

      p {
        width: 60%;
        margin: rem-calc(25) auto 0;
      }
    }
    &__list {
      padding: rem-calc(40) rem-calc(35);

      dt {
        font-size: rem-calc(30);
      }
      dd {
        padding-top: rem-calc(15);
      }
    }
  }
}
