.actionnaireCarnet {
  &__listInfor,
  &__static {
    margin-bottom: rem-calc(20);

    > .inner {
      padding: 0 rem-calc(40*2/3) rem-calc(50*2/3);
      background-color: $white;
    }

    @media #{$large-up} {
      background-color: $white;
      > .inner {
        padding: 0 rem-calc(20*2/3);
      }
    }
    .heading {
      padding-bottom: rem-calc(35*2/3);
      margin-bottom: rem-calc(35*2/3);
      border-bottom: 1px solid $mediumGrey;
    }
  }

  &__listInfor {
    &__list {
      margin: 0;
      @include display(flex);
      @include flex-wrap(wrap);

      p {
        margin: 0;
      }

      dt {
        width: 62.5%;
        margin-bottom: rem-calc(25*2/3);
        font-family: $font-bold;

        p {
          color: $jet;
        }
      }
      dd {
        width: 37.5%;
        margin-bottom: rem-calc(25*2/3);
        font-family: $font-bold;

        p {
          color: $blue;
        }
      }
    }
  }

  &__static {
    &__img {
      padding: rem-calc(15) 0;

      img {
        display: block;
        width: 100%;
      }
    }

    &__chartInfor {
      ul {
        margin: 0;
        list-style: none;
      }
      li {
        text-transform: uppercase;
        padding-top: rem-calc(30*2/3);

        &.public p:before {
          background-color: #a1d7f1;
        }
        &.employes p:before {
          background-color: $pink;
        }
        &.kauman p:before {
          background-color: $brown;
        }
        &.partner p:before {
          background-color: $blue;
        }
      }
      p {
        position: relative;
        padding-left: rem-calc(35);
        margin: 0;
        color: $blue;

        &:before {
          content: "";
          position: absolute;
          top: rem-calc(4);
          left: 0;
          display: inline-block;
          width: rem-calc(17);
          height: rem-calc(15);
          vertical-align: middle;
          background-color: $blue;
        }
      }
      .percent {
        font-family: $font-bold;
        color: $jet;
      }
    }

    &__note {
      ol {
        margin: 0;
        padding-top: rem-calc(45*2/3);
        counter-reset: list;
        > li {
          list-style: none;
          font-size: rem-calc(12);
          font-style: italic;
          color: $grey;

          &:before {
            content: counter(list) ") ";
            counter-increment: list;
          }
        }
      }
    }
  }
}
@media #{$medium-up} {
  .actionnaireCarnet {
    &__listInfor,
    &__static {
      > .inner {
        padding: 0 rem-calc(20) rem-calc(50*2/3);
      }
    }
    &__listInfor {
      &__list {
        dt {
          width: 78%;
        }
        dd {
          width: 22%;
        }
      }
    }
    &__static {
      &__img {
        padding-top: rem-calc(15);
      }
      &__chartInfor {
        ul {
          width: 93%;
          @include display(flex);
          @include flex-wrap(wrap);
          @include justify-content(space-between);
        }
        p {
          &:before {
            top: rem-calc(5);
          }
        }
        @media #{$medium-only} {
          p {
            font-size: rem-calc(12);
            padding-left: rem-calc(25);

            &:before {
              top: rem-calc(4);
              width: rem-calc(13);
              height: rem-calc(12);
            }
          }
        }
      }
    }
  }
}
