.programContactInformation {
  background: $lightGrey;
  padding-top: rem-calc(30*2/3);
  margin: $column-gutter-small 0;
  text-align: center;
  &__heading {
    font-weight: $font-weight-bold;
    color: $primary-color;
    padding: 0 $column-gutter-small;
  }
  &__item {
     padding: rem-calc(30*2/3) $column-gutter-small;
    &:first-of-type {
      border-bottom: 1px solid $mediumGrey;
    }
    .icon {
      color: $grey;
      font-size: rem-calc(40*2/3);
      vertical-align: middle;
      margin-right: rem-calc(15*2/3);
    }
    .text {
      color: $secondary-color;
    }
  }
  &__mail {
    display: block;
    .text {
      font-family: $font-bold;
    }
  }
  &__phone {
    line-height: 1.3;
    &:last-child {
      &:before {
        content: '/';
        display: inline-block;
        margin: 0 rem-calc(5*2/3);
        color: $secondary-color;
        position: relative;
      }
    }

    .icon {
      @include transform(rotate(-30deg));
    }
  }
}
.programDelivery {
  padding: rem-calc(15*2/3) 0;
  border-bottom: 1px solid $mediumGrey;
  border-top: 1px solid $mediumGrey;
  @include display(flex);
  @include align-items(stretch);
  @include justify-content(flex-start);
  &__column {
    &:first-of-type {
      padding-right: rem-calc(30*2/3);
      border-right: 1px solid $mediumGrey;
    }
    &:last-of-type {
      padding-left: rem-calc(30*2/3);
    }
    p {
      color: $jet;
      font-size: rem-calc(22*2/3);
      margin-bottom: 0;
    }
  }
}

@media #{$medium-up} {
  .programContactInformation {
    &__content {
      @include display(flex);
      @include align-items(stretch);
      @include justify-content(space-around);
      text-align: left;
    }
    &__item {
      width: 50%;
      padding-top: rem-calc(10);
      padding-bottom: rem-calc(10);
      margin: rem-calc(20) 0;
      &:first-of-type {
        border-bottom: 0;
        border-right: 1px solid $mediumGrey;
      }
    }
  }
  .programDelivery {
    padding-top: rem-calc(20);
    padding-bottom: rem-calc(20);
  }
}
