.legroupeFinaceCalendar {
  &__list {
    ul {
      list-style: none;
      margin: 0;
    }
    li {
      padding: rem-calc(15*2/3) 0;
    }
  }
  &__item {
    background-color: $white;
    padding: rem-calc(30*2/3) 0;

    .inner {
      display: table;
      width: 100%;
    }

    .date {
      display: table-cell;
      width: percentage(130/580);
      text-align: center;
      vertical-align: middle;
      border-right: 1px solid $grey;
      font-family: $font-bold;

      span {
        display: block;
      }
      .day {
        font-size: rem-calc(40*2/3);
        line-height: rem-calc(35);
        color: $jet;
      }
      .month {
        font-size: rem-calc(18*2/3);
        line-height: 1.7;
        color: $blue;
      }
    }

    .description {
      display: table-cell;
      vertical-align: middle;
      padding-left: percentage(30/580);
      color: $grey;
    }
  }

  .text-infor {
    padding-top: rem-calc(20);
    font-style: italic;
    color: $grey;
  }
}
@media #{$medium-up} {
  .legroupeFinaceCalendar {
    &__list {
      ul {
        @include display(flex);
        @include flex-wrap(wrap);
        @include justify-content(space-between);
        max-width: 100%
      }
      li {
        width: 49%;
      }

    }
    &__item {
      padding: rem-calc(30*2/3);

      .date {
        padding-right: rem-calc(15);

        .day {
          font-size: rem-calc(40);
        }
        .month {
          font-size: rem-calc(18);
        }
      }
    }
  }
}
