.partager {
  @media #{$medium-up} {
    @include display(flex);
    font-size: 1.25rem;
  }

  &__container {
    margin-bottom: rem-calc(48);
    @media #{$medium-up} {
      @include flex(1);
    }

    .heading {
      display: inline-block;
      .heading__title {
        display: inline;
      }

      @media #{$medium-up} {
        @include display(flex);
        @include flex-flow(column nowrap);
        font-size: 1.25rem;
        span {
          display: block;
        }
      }
      span {
        font-weight: bold;
      }
    }

    .heading__title--sub {
      @media #{$medium-up} {
        display: none;
      }
    }

    &__left {
      @media #{$medium-up} {
        margin-right: rem-calc(40);
      }
    }

    &__right {
      @media #{$medium-up} {
        margin-left: rem-calc(40);
      }
    }
  }

  &__selections {
    li {
      margin-bottom: rem-calc(32);
    }
  }

  &__prospects {
    background-color: $lightGrey;
    padding: rem-calc(26) rem-calc(16);
    li {
      margin-bottom: rem-calc(16);
    }

    .label-checkbox:before {
      margin-right: rem-calc(16);
    }
  }

  &__message {
    padding: rem-calc(16);
    background-color: $lightGrey;

    &:invalid:not(:focus) {
      text-align: center;
    }
  }

  &__selections, &__prospects {
    li:last-child {
      margin-bottom: 0;
    }
  }

  textarea {
    height: rem-calc(228);
    resize: none;
  }
}

.partager.message, .partager.selections {
  @media #{$medium-up} {
    .partager__container__right {
      @include display(flex);
      @include flex-flow(column nowrap);
      .expand {
        height: auto;
        @include flex(1);
      }
    }
  }
}

.partager__selections li {
  @media #{$medium-up} {
    padding-top: rem-calc(16);
    border-top: rem-calc(1) solid $mediumGrey;
    padding-bottom: rem-calc(16);
    margin-bottom: 0;

    &:last-child {
      border-bottom: rem-calc(1) solid $mediumGrey;
    }
  }
}

.partager__list {
  margin-left: 0;

  @media #{$medium-up} {
    margin-bottom: 0;
  }

  li {
    @include display(flex);
  }

  &__checkbox, &__images {
    padding-right: rem-calc(16);
  }

  &__checkbox {
    .label-checkbox {
      padding: 0;
    }
  }

  &__images {
    @include flex(2);
    @media #{$very-small-only} {
      display: none;
    }

    img {
      width: 100%;
    }
  }

  &__content {
    @include display(flex);
    @include flex-flow(column nowrap);
    @include flex(3);
    > * {
      margin-bottom: 1rem;
      p {
        margin-bottom: 0;
      }

      &:last-child {
        margin-bottom: 0;
      }
    }

    &__title {
      .heading__title {
        font-weight: 100;
      }
    }

    .heading__title {
      @media #{$medium-up} {
        font-size: 1rem;
      }
    }

    &__description {
      @include flex(1);
      @include display(flex);
      @include align-items(center);
    }
  }
}

.jet {
  color: $jet;
}
