.mySelectionsProgrammes {
  .accordion__link {
    margin-bottom: rem-calc(20*2/3);
  }

  &__actions {
    @extend .ul-unstyled;
    @include display(flex);
    @include flex-direction(column);
    position: absolute;
    right: ($column-gutter-small / 2);
    z-index: 1;

    .display-status {
      @include position(relative, null 0 null null);
      margin-bottom: rem-calc(20*2/3);
    }
  }

  .opened {
    .mySelectionsProgrammes__actions {
      @include flex-direction(column);
      @include align-items(flex-end);

      [data-app-accordion-link] {
        visibility: hidden;
        position: absolute;
      }
    }

    .mySelectionsProgramme__small {
      opacity: 0;
    }
    .mySelectionsProgramme__large {
      opacity: 1;
    }
  }

  > :last-child {
    margin-bottom: 0;
  }
}

.mySelectionsProgramme {
  .prices {
    @extend .ul-unstyled;

    li {
      @include display(flex);
      @include justify-content(space-between);
      @include align-items(center);
      padding: rem-calc(15*2/3) 0;
      border-top: 1px solid $mediumGrey;
    }

    .text {
      color: $grey;
      margin-right: rem-calc(10*2/3);
      margin-bottom: 0;
      display: inline-block;
      vertical-align: middle;
      font-size: rem-calc(24*2/3);

      strong {
        font-size: rem-calc(35*2/3);
      }
    }
  }

  &__small {
    opacity: 1;
    @include transition-property(opacity);
    @include transition-duration(.3s);
    @include transition-timing-function(ease-out);

    .media {
      margin-bottom: rem-calc(35*2/3);

      &__img {
        width: rem-calc(150*2/3);
      }

      &__content {
        padding-right: rem-calc(55*2/3);

        .heading {
          padding-top: 0;
          margin: rem-calc(-4) 0 rem-calc(20*2/3) 0;

          &__title--sub {
            margin-bottom: rem-calc(18*2/3);
          }
        }
      }
    }

    .prices {
      li:last-child {
        padding-bottom: 0;
      }
    }
  }

  &__large {
    @include transition-property(opacity);
    @include transition-duration(.3s);
    @include transition-timing-function(ease-out);
    padding-top: rem-calc(40);
    opacity: 0;

    .heading {
      padding: rem-calc(20*2/3) 0;
      margin-top: rem-calc(25*2/3);

      .heading__title {
        font-size: rem-calc(30*2/3);
      }

      @media #{$small-only} {
        &:before {
          content: "";
          @include position(absolute, 0 null null 0);
          width: rem-calc(45*2/3);
          height: rem-calc(4);
          background: $jet;
          @include flex-basis(0);
        }
      }
    }

    .text {
      margin-bottom: rem-calc(30*2/3);
      .heading__title {
        margin-bottom: rem-calc(10*2/3);
      }
    }

    .squaredImageItem__actions {
      padding: rem-calc(35*2/3) 0 0;
      border-top: 1px solid $mediumGrey;
      @extend .ul-unstyled;
      .btn-rounded {
        margin-bottom: rem-calc(15*2/3);
        display: block;
      }

      .sharing {
        width: initial;
        max-width: none;
      }

      .sharing__items {
        &__item {
          font-size: rem-calc(50);
        }
      }
    }
  }

  .contact {
    @extend .ul-unstyled;
    padding: rem-calc(30*2/3) 13%;

    &__item {
      > * {
        margin-bottom: rem-calc(15*2/3);
        display: block;
      }

      &:last-child {
        > * {
          margin-bottom: 0;
        }
      }
    }

    .text {
      text-align: center;
      color: $jet;
      font-family: $font-bold;
      font-weight: $font-weight-normal;
      margin-bottom: rem-calc(30*2/3);
      strong {
        display: block;
        color: $blue;
      }
    }
  }
}

@media #{$small-only} {
  .mySelectionsProgramme {
    .sharing {
      @include display(flex);
      @include justify-content(center);
      position: static;
      opacity: 1;
      box-shadow: none;
      border: none;
      @include transform(translateY(0));
      padding: 0;
      max-width: none;
      &:after,
      &:before {
        display: none;
      }
    }
  }
}


@media #{$medium-up} {
  .mySelectionsProgrammes {

    .accordion__link {
      @include display(flex);
      @include align-items(center);
      @include flex-direction(row-reverse);
      @include justify-content(space-between);

      .mySelectionsProgrammes__actions {
        position: static;
        @include flex-direction(row);
        padding-left: rem-calc(50);

        [data-app-accordion-link] {
            margin-right: rem-calc(20);
          }

        > * {
          margin-bottom: 0;
        }
      }

      &.opened {
        .mySelectionsProgrammes__actions {
          position: absolute;
          display: block;
          right: rem-calc(20);
          top: rem-calc(20);
          padding-left: 0;
        }
      }
    }
  }

  .mySelectionsProgramme {
    @include flex(1 1 0%);

    &__small {
      @include display(flex);
      @include justify-content(space-between);
      @include align-items(center);

      .media {
        margin-bottom: 0;
        @include align-items(center);
        @include flex(2.4 1 auto);
        width: 50%;

        &__content {
          @include display(flex);
          @include align-items(center);
          @include flex(1 1 0%);

          .heading {
            padding-right: 5%;
            margin-bottom: 0;
            &__title {
              font-size: rem-calc(18);
            }
          }

          .text .heading__title {
            font-size: rem-calc(16);
          }
        }
      }
    }

    &__large {
      @include display(flex);
      @include justify-content(space-between);
      padding-top: 0;

      .heading--bordered:before {
        display: none;
      }

      .squaredImageItem {
        &__img {
          width: 26%;
        }

        &__infos {
          width: 46%;
          padding: 0;
          @include display(flex);
          @include flex-direction(column);
        }

        &__details {
          margin-bottom: rem-calc(20);

          .heading {
            margin-top: 0;
            padding-top: rem-calc(5);
          }
          .heading__title {
            font-size: rem-calc(30);
          }

          .text .heading__title {
            font-size: rem-calc(16);
          }
        }

        &__actions {
          @include display(flex);
          @include justify-content(flex-start);

          > * {
            @include display(flex);
            @include align-items(stretch);

            &:first-child {
              margin-right: rem-calc(10);
            }
            &:last-child {
              margin-left: rem-calc(10);
              > * {
                white-space: nowrap;
              }
            }
            .icon {
              vertical-align: middle;
            }
          }

          .sharing__items__item {
            font-size: rem-calc(30);
          }

          .btn-rounded {
            margin-bottom: 0;
          }
        }
      }

      .contact {
        width: rem-calc(260);
        padding: rem-calc(20);
        margin-top: rem-calc(50);

        &__item {
          &:first-child {
            margin-bottom: rem-calc(20);
          }

          > * {
            text-align: left;
          }

          .text {
            font-family: $font-regular;
            text-align: left;
            margin-bottom: 0;
            font-size: rem-calc(14);
            strong {
              color: $jet;
            }
          }

          &__img {
            float: left;
            margin-right: rem-calc(10);
          }

          .btn-phone {
            border-radius: 0;
            box-shadow: none;
            border: none;
            text-align: left;
            padding: 0;
            color: $jet;
            font-size: rem-calc(14);

            .no-touch &:hover {
              background-color: transparent;
              color: $blue;
            }
          }
        }
      }

      .prices {
        display: inline-block;
      }
    }

    .sharing {
      .icon {
        padding: 0;
      }
    }

    .prices {
      @include flex(1 1 20%);
      li {
        border: none;
        padding: 0;
      }

      .text {
        margin-right: rem-calc(20);
      }
    }
  }
}


@media #{$medium-only} {
  .mySelectionsProgrammes {
    .accordion__link .mySelectionsProgrammes__actions {
      padding-left: rem-calc(20);
    }
  }

  .mySelectionsProgramme {
    &__small {
      .prices {
        li:first-child {
          margin-bottom: rem-calc(10);
        }
        .text {
          strong {
            font-size: rem-calc(16);
          }
        }
      }

      .media__content {
        padding-right: rem-calc(20);
      }
    }

    &__large {
      display: block;

      .squaredImageItem {
        &__img {
          display: inline-block;
          width: 31%;
          margin-right: 3%;
        }

        &__infos {
          display: inline-block;
          width: 65%;
          vertical-align: top;
        }

        &__actions {
          > li {
            &:nth-child(-n+2) {
              max-width: 37%;
            }
          }
        }
      }

      .contact {
        width: 100%;
        display: table;
        table-layout: fixed;
        margin-top: rem-calc(20);
        > li {
          display: table-cell;
          vertical-align: middle;
          margin-bottom: 0;
          padding-right: rem-calc(10);

          &:last-child {
            padding-right: 0;
          }

          > * {
            margin-bottom: 0;
          }
        }
      }

      .prices {
        .text {
          margin-right: rem-calc(20);
        }
      }
    }
  }
}


.articles {
  @media #{$medium-up} {
    @include display(flex);
    @include flex-wrap(wrap);
  }

  article {
    display: block;
    height: auto;
    padding: 0 0.55556rem 1.11111rem;

    @media #{$medium-only} {
      width: 33.33333%;
    }

    @media #{$large-up} {
      width: 25%;
    }
  }
}


.searches {
  list-style: none;
  margin-left: 0;
  margin-bottom: 0;

  ul {
    list-style: none;
    margin-left: 0;
  }

  .search {
    position: relative;
    background: $white;
    margin-bottom: rem-calc(20);
    padding: ($column-gutter-small / 2);
    width: 100%;

    &__desc {
      padding-right: rem-calc(30);
    }

    &__type {
      > li {
        display: inline-block;
        color: $blue;
        margin-right: rem-calc(10);

        &:last-child {
          margin-right: 0;
        }

        &:first-child {
          color: $jet;
        }
      }
    }

    &__desc {
      @include flex(1 1 0%);
    }

    &:last-child {
      margin-bottom: 0;
    }

    &__type,
    &__options > li {
      font-size: rem-calc(24*2/3);
      font-family: $font-bold;
      @media #{$medium-up} {
        font-size: rem-calc(24);
      }
    }

    &__options {
      > li {
        display: inline-block;
        vertical-align: middle;
        margin-right: rem-calc(10*2/3);
        color: $grey;

        @media #{$medium-up} {
          margin-right: rem-calc(15);
        }
      }
    }

    &__tools {
      @media #{$medium-up} {
        @include display(flex);
        @include align-items(center);
      }

      &__exec {
        margin-top: rem-calc(16);
        margin-bottom: 0;
        width: 100%;

        @media #{$medium-up} {
          margin-right: rem-calc(16px);
          margin-top: 0;
          width: auto;
        }
      }

      &__remove {
        @media #{$small-only} {
          @include position(absolute, ($column-gutter-small / 2) ($column-gutter-small / 2) null null);
        }
      }
    }

    @media #{$medium-up} {
      @include display(flex);
      @include justify-content(space-between);
      @include align-items(center);
      @include flex-direction(row);
      padding: rem-calc(20);

      &__type {
        @include display(flex);

        > li {
          margin-right: rem-calc(10);
        }
      }

      &__desc {
        @include flex(1 1 0%);
      }
    }
  }
}
