.postuler {

  &__item {
    position: relative;
    background: $white;
    border-bottom: 1px solid $mediumGrey;
    padding: rem-calc(40*2/3) 0 0 0;
    margin-bottom: 0;

    &__content {
      margin-bottom: rem-calc(40*2/3);

      p:last-child{
        margin-bottom: 0;
      }
    }
  }

  .accordion__link {
    font-family: $font-regular;
  }

  .display-status {
    @include position(absolute, rem-calc(40*2/3) 0 null null);
    opacity: 1;
    @include transition-property(all);
    @include transition-duration(.3s);
    @include transition-timing-function(ease-out);
    &.active {
      opacity: 0;
    }
  }

  &__details {
    @extend .ul-unstyled;
    color: $grey;
  }

  [data-app-accordion-content] {
    margin-bottom: rem-calc(40*2/3);
  }

  .btn-wrapper {
    padding-top: rem-calc(40*2/3);
    margin-bottom: 0;
    a {
      margin-bottom: 0;
    }
  }

  @media #{$medium-up} {
    &__item {
      @include display(flex);
      @include flex-wrap(nowrap);
      padding: rem-calc(40) 0 0 0;

      .latestJobs__item__heading {
        width: 30%;
      }

      &__content {
        width: 70%;
        margin-bottom: rem-calc(40);
      }
    }

    &__details {
      padding-right: rem-calc(50);
    }

    .display-status {
      top: 50%;
      @include transform(translateY(-50%));
    }

    .btn-wrapper {
      text-align: left;
    }
  }

  @media #{$large-up} {
    &__item {
      .latestJobs__item__heading {
        width: 40%;
      }

      &__content {
        width: 60%;
      }
    }
  }
}

.postuler-popin {
  margin-bottom: rem-calc(20);

  &__intro {
    font-family: $font-bold;
  }

  &__intro,
  &__details {
    font-size: rem-calc(20);
  }
}

