.b2b .siteFooter {
  background-color: $lightGrey;
  padding: 0;
  
  @media #{$medium-up} {
    background-color: $white;
  }

  .wrapper {
    @media #{$medium-up} {
      @include display(flex);
    }
  }

  ul {
    display: block;
    list-style: none;
    margin: 0;

    @media #{$medium-up} {
      float: right;
      width: auto;
    }
  }

  li {
    margin: 0;

    @media #{$medium-up} {
      display: inline;
      float: left;
      margin-left: rem-calc(32);
      text-align: right;
    }
  }

  a {
    color: $jet;
    font-family: Helvetica Neue;
    font-weight: normal;
    text-align: center;
    
    @media #{$medium-up} {
      color: $grey;
      text-align: left;

      &:hover {
        color: $blue;
      }
    }
  }

  .siteFooter__head {
    margin: 0;
    @media #{$medium-up} {
      @include flex(1);
    }
  }

  .siteFooter__nav {
    @media #{$medium-up} {
      @include flex(2);
    }
  }

  .logo {
    margin: 0 auto;

    @media #{$medium-up} {
      width: 70%;
    }

    img {
      vertical-align: baseline;
      width: 84%;
    }
  }

  .siteFooter__head .logo,
  li {
    line-height: rem-calc(64);
  }

  .siteFooter__head,
  li {
    border-bottom: 1px solid $mediumGrey;
    @media #{$medium-up} {
      border-bottom: 0;
    }
  }
  
  @media #{$medium-up} {
    .siteFooter__nav ul {
    }
  }
}


