.popinCookies {
  @include position(fixed, null 0 0 0);
  width: 100%;
  z-index: 99999;
  background: $lightGrey;
  display: none;

  &__text,
  &__buttons {
    padding-top: rem-calc(10);
    padding-bottom: rem-calc(10);
  }

  &__text {
    padding-bottom: 0;
    p {
      font-size: rem-calc(14);
      font-weight: normal;
      color: $jet;
      margin-bottom: 0;
      -webkit-text-size-adjust: none;
    }
  }

  &__buttons {
    @include display(flex);
    @include justify-content(space-between);
    @include align-items(center);
    margin-left: rem-calc(20);
    cursor: pointer;

    &__agree {
      margin: 0;
      display: block;
    }

    &__close {
      display: block;
      margin: 0 auto 0 rem-calc(50);
      width: rem-calc(30);
      height: rem-calc(30);
      background: transparent;
      border: 1px solid $mediumGrey;
      color: $blue;
      text-transform: uppercase;
    }
  }

  @media #{$small-only} {
    &__text {
      padding-right: rem-calc(5);
    }
    &__buttons {
      margin-left: 0;

      &__agree {
        margin: 0;
        width: 50%;
      }

      &__close {
        margin: 0 auto;
      }
    }
  }

  @media #{$medium-up} {
    .wrapper {
      @include display(flex);
      @include justify-content(space-between);
      @include align-items(center);
    }

    &__text,
    &__buttons {
      padding-top: rem-calc(30);
      padding-bottom: rem-calc(30);
    }

    &__text {
      @include flex(6);
    }
    &__buttons {
      @include flex(1);
    }
  }
}
